'use client';

import { Fragment, useMemo } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";

import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
} from "@/components/ui/breadcrumb";

function toTitleCase(segment) {
  return decodeURIComponent(segment)
    .replace(/-/g, " ")
    .replace(/\b\w/g, (char) => char.toUpperCase());
}

export function DynamicBreadcrumb({
  rootHref = "/",
  rootLabel = "Home",
  overrides = {},
  hideRootOnMobile = false,
}) {
  const pathname = usePathname();

  const segments = useMemo(() => {
    if (!pathname) {
      return [];
    }

    return pathname
      .split("/")
      .filter(Boolean);
  }, [pathname]);

  const crumbs = useMemo(() => {
    return segments.map((segment, index) => {
      const href = `/${segments.slice(0, index + 1).join("/")}`;
      const label = overrides[segment] ?? toTitleCase(segment);

      return { href, segment, label };
    });
  }, [overrides, segments]);

  const hasCrumbs = crumbs.length > 0;

  return (
    <Breadcrumb>
      <BreadcrumbList>
        <BreadcrumbItem className={hideRootOnMobile ? "hidden md:inline-flex" : undefined}>
          {hasCrumbs ? (
            <BreadcrumbLink asChild>
              <Link href={rootHref}>
                {rootLabel}
              </Link>
            </BreadcrumbLink>
          ) : (
            <BreadcrumbPage>
              {rootLabel}
            </BreadcrumbPage>
          )}
        </BreadcrumbItem>

        {crumbs.map((crumb, index) => {
          const isLast = index === crumbs.length - 1;

          return (
            <Fragment key={crumb.href}>
              <BreadcrumbSeparator className={hideRootOnMobile && index === 0 ? "hidden md:inline-flex" : undefined} />
              <BreadcrumbItem>
                {isLast ? (
                  <BreadcrumbPage>
                    {crumb.label}
                  </BreadcrumbPage>
                ) : (
                  <BreadcrumbLink asChild>
                    <Link href={crumb.href}>
                      {crumb.label}
                    </Link>
                  </BreadcrumbLink>
                )}
              </BreadcrumbItem>
            </Fragment>
          );
        })}
      </BreadcrumbList>
    </Breadcrumb>
  );
}
